export const SvgSlideshow = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [path, setPath] = useState(randomPath());

  return (
    <motion.svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 500 500"
      className="item"
      onClick={() => {}}
    >
      <motion.path
        d={path}
        variants={icon}
        initial="hidden"
        animate={isOpen ? 'hidden' : 'visible'}
        transition={{
          default: {
            delay: Math.random() * 2,
            duration: 4,
            ease: isOpen ? 'easeOut' : 'easeIn',
            repeatDelay: 0,
          },
        }}
        onAnimationComplete={() => {
          if (!isOpen) {
            setIsOpen(!isOpen);
          } else {
            setIsOpen(!isOpen);
            setPath(randomPath());
          }
        }}
      />
    </motion.svg>
  );
};
